<template>
  <demo-article>
    <p></p>
    <example title="">
      <div slot="demo">
        <XForm label-width="100px" label-position="right" :model="form" :rules="rules" ref="form">
          <XFormItem label="" prop="name">
            <x-input v-model="form.name" :width="200"></x-input>
          </XFormItem>
          <XFormItem label="" prop="province">
            <x-select v-model="form.province" style="width: 200px;">
              <x-option v-for="item in provinces" :value="item.value" :key="item">{{ item.label }}</x-option>
            </x-select>
          </XFormItem>
          <XFormItem label="" prop="cascader">
            <cascader style="width: 200px;" :data="spots" v-model="form.cascader"></cascader>
          </XFormItem>
          <XFormItem label="" prop="radios">
            <radio-group v-model="form.radios">
              <radio label="IOS"></radio>
              <radio label="Android"></radio>
              <radio label="Windows"></radio>
            </radio-group>
          </XFormItem>
          <XFormItem label="" prop="mutiple">
            <checkbox v-model="form.mutiple"></checkbox>
          </XFormItem>
          <XFormItem label="" prop="mutipleGroup">
            <checkbox-group v-model="form.mutipleGroup">
              <checkbox label="iOS"></checkbox>
              <checkbox label="Android"></checkbox>
              <checkbox label="Windows"></checkbox>
            </checkbox-group>
          </XFormItem>
          <XFormItem label="" prop="date">
            <date-picker
              style="width: 200px"
              type="date"
              placeholder=""
              v-model="form.date">
            </date-picker>
          </XFormItem>
          <XFormItem label="" prop="time">
            <time-picker
              style="width: 168px;"
              type="time"
              placeholder=""
              v-model="form.time">
            </time-picker>
          </XFormItem>
          <XFormItem label="" prop="switch">
            <x-switch v-model="form.switch"></x-switch>
          </XFormItem>
          <XFormItem label="" prop="discription">
            <x-input v-model="form.discription" type="textarea"></x-input>
          </XFormItem>
          <XFormItem>
            <x-button type="primary" @click.native="submitForm"></x-button>
            <x-button></x-button>
          </XFormItem>
        </XForm>
      </div>
      <div slot="desc">
        copy from <a src="http://element.eleme.io/#/zh-CN/component/form">element ui</a>
      </div>
      <x-code lang="html" slot="code">{{ code.base }}</x-code>
      <x-code lang="js" slot="code">{{ code.baseData }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/form'

export default {
  name: 'FormDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
        discription: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 14, message: ' 3  14 ', trigger: 'blur' }
        ],
        province: [
          { required: true, message: '', trigger: 'change' }
        ],
        radios: [
          { required: true, message: '', trigger: 'change' }
        ],
        mutiple: [
          { required: true, message: '', trigger: 'change', type: 'boolean' }
        ],
        mutipleGroup: [
          { required: true, message: '', trigger: 'change', type: 'array' },
          { validator: this.customMutipleValidate, trigger: 'change' }
        ],
        date: [
          { required: true, message: '', trigger: 'change' }
        ],
        time: [
          { required: true, message: '', trigger: 'change' }
        ],
        cascader: [
          { required: true, trigger: 'change', type: 'array' },
          { validator: this.customValidate, trigger: 'change' }
        ],
        switch: [
          { required: true, trigger: 'change', type: 'boolean' }
        ]
      },
      form: {
        name: '',
        province: '',
        radios: 'IOS',
        mutiple: true,
        mutipleGroup: [],
        switch: true,
        date: new Date(),
        time: new Date(),
        discription: '',
        cascader: [{
          value: 'beijing',
          label: ''
        }, {
          value: 'tiantan',
          label: ''
        }]
      },
      spots: [{
        value: 'beijing',
        label: '',
        children: [{
          value: 'gugong',
          label: ''
        }, {
          value: 'tiantan',
          label: ''
        }, {
          value: 'wangfujing',
          label: ''
        }]
      }, {
        value: 'jiangsu',
        label: '',
        children: [{
          value: 'nanjing',
          label: '',
          children: [{
            value: 'fuzimiao',
            label: ''
          }]
        }, {
          value: 'suzhou',
          label: '',
          children: [{
            value: 'zhuozhengyuan',
            label: ''
          }, {
            value: 'shizilin',
            label: ''
          }]
        }]
      }],
      provinces: [{
        value: '',
        label: ''
      }, {
        value: 'anhui',
        label: ''
      }, {
        value: 'shanghai',
        label: ''
      }]
    }
  },
  methods: {
    submitForm () {
      this.$refs.form.validate()
    },
    customValidate (rule, value, callback) {
      if (!value.length) {
        callback(new Error(''))
      } else if (value.length === 1) {
        callback(new Error('/'))
      }
    },
    customMutipleValidate (rule, value, callback) {
      if (!value.length) {
        callback(new Error(''))
      }
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



